/* Blurry overlay */
.image-viewer-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(10px);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    transition: opacity 0.3s ease-in-out;
}

.image-viewer-content {
    position: relative;
    max-width: 90%;
    max-height: 100%;
    text-align: center;
    transition: transform 0.3s ease-in-out;
}

.image-and-title-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

/* Image sliding and fading */
.image-viewer-image {
    max-width: 100%;
    max-height: 90vh;
    object-fit: contain;
    opacity: 1;
    transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
    border-radius: 3px;
}

/* Slide right animation */
.image-viewer-image.right {
    transform: translateX(0.55vw);
    opacity: 0;
    animation: slideInRight 0.3s forwards ease-in-out
}

@keyframes slideInRight {
    from {
        transform: translateX(0.55vw);
        opacity: 0;
    }

    to {
        transform: translateX(0);
        opacity: 1;
    }
}

/* Slide left animation */
.image-viewer-image.left {
    transform: translateX(-0.55vw);
    opacity: 0;
    animation: slideInLeft 0.3s forwards ease-in-out
}

@keyframes slideInLeft {
    from {
        transform: translateX(-0.55vw);
        opacity: 0;
    }

    to {
        transform: translateX(0);
        opacity: 1;
    }
}

/* Title below image */
.image-viewer-title {
    margin-top: 10px;
    padding: 5px 10px;
    width: fit-content;
    align-self: center;
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    font-size: 14px;
    border-radius: 3px;
    transition: width 0.3s ease-in-out;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    display: inline-block;
}

/* Close button in the top-right corner */
.close-btn {
    position: fixed;
    width: 2.5rem;
    height: 2.5rem;
    top: 20px;
    right: 20px;
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    border: none;
    border-radius: 50%;
    padding: 10px;
    cursor: pointer;
    transition: all 0.2s ease;

    display: flex;
    justify-content: center;
    align-items: center;
}

.close-btn:hover {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.close-btn svg {
    /* Target the icon inside the Pressable */
    color: rgb(175, 175, 175);
    /* Initial color */
    transition: color 0.3s ease;
    /* Smooth transition */
}

.close-btn:hover svg {
    color: white;
    /* Color on hover */
}

/* Navigation buttons for previous/next */
.nav-btn {
    width: 2.5rem;
    height: 2.5rem;
    position: fixed;
    top: 50%;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border: none;
    border-radius: 60%;
    padding: 10px;
    cursor: pointer;
    transition: all 0.2s ease;

    display: flex;
    justify-content: center;
    align-items: center;
}

.nav-btn svg {
    /* Target the icon inside the Pressable */
    color: rgb(175, 175, 175);
    /* Initial color */
    transition: color 0.3s ease;
    /* Smooth transition */
}

.nav-btn:hover svg {
    color: white;
    /* Color on hover */
}

.nav-btn:hover {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

/* Previous button */
.left-btn {
    left: 20px;
}

/* Next button */
.right-btn {
    right: 20px;
}

/* Smooth fade in and fade out effect */
.image-viewer-overlay {
    opacity: 1;
    animation: fadeIn 0.3s ease-in-out;
}

/* Smooth fade in and fade out effect */
.fadeIn {
    opacity: 1;
    animation: fadeIn 0.3s ease-in-out forwards;
}

.fadeOut {
    opacity: 0;
    animation: fadeOut 0.3s ease-in-out forwards;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fadeOut {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}