/* src/components/Header/Header.css */

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1vw;
  z-index: 100;
  background-color: transparent;
  /* Transparent initially */
  transition: height 0.75s ease, padding 0.75s ease, background-color 2s ease, backdrop-filter 2s ease;
  backdrop-filter: blur(0px);
}

.header.scrolled {
  background-color: rgba(0, 0, 0, 0.831);
  backdrop-filter: blur(5px);
  /* Opaque on scroll */
}

/* New styles when 67% of the hero section is scrolled */
.header.scrolled-hero {
  padding-left: 1vw;
  padding-right: 1vw;
  height: 35px;
}

.logo {
  color: white;
  font-size: 20px;
  font-weight: 400;
  /* Bold */
  letter-spacing: 0.5px;
  cursor: pointer;
  transition: font-size 0.75s ease;
}

.logo.scrolled-hero {
  font-size: 20px;
}

.nav-links {
  display: flex;
  gap: 20px;

}

.nav-links a {
  color: white;
  text-decoration: none;
  font-size: 0.9rem;
  font-weight: 400;
  /* Bold */
  letter-spacing: 0.5px;
  cursor: pointer;
  transition: color 0.3s ease, font-size 0.7s ease;
}

.nav-links.scrolled-hero a {
  font-size: 0.9rem;

}

.nav-links a:hover {
  color: #ddd;
}

.hamburger {
  display: none;
  flex-direction: column;
  gap: 5px;
  cursor: pointer;
}

.hamburger .line {
  width: 25px;
  height: 3px;
  background-color: white;
  transition: all 0.3s ease;
}

@media (max-width: 1500px) {

  .header.scrolled-hero {
    padding-left: 1vw;
    padding-right: 1vw;
    height: 35px;
  }
}

/* Mobile styles */
@media (max-width: 768px) {
  .nav-links {
    position: absolute;
    top: 35px;
    right: 0;
    background-color: rgba(0, 0, 0, 0.8);
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    display: none;
    backdrop-filter: blur(5px);
  }

  .nav-links.open {
    display: flex;
  }

  .hamburger {
    display: flex;
  }

}

@media (max-width: 600px) {

  /* New styles when 67% of the hero section is scrolled */
  .header.scrolled-hero {
    padding-left: 2.5vw;
    padding-right: 2.5vw;
    height: 35px;
  }

  .header {
    padding-left: 2.5vw;
    padding-right: 2.5vw;
    height: 35px;
  }
}

/* .close-icon{

} */

.close-icon {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 3rem;
  color: rgb(185, 185, 185);
  cursor: pointer;
  transition: color 0.3s ease, text-shadow 0.3s ease;
  text-shadow: 0 0 20px rgba(255, 255, 255, 0.5), 0 0 20px rgba(255, 255, 255, 0.5);
  height: 50px;
  width: 50px;
  display: flex; /* Use flexbox */
  align-items: center; /* Vertically center */
  justify-content: center; /* Horizontally center */
}

/* .nav-links-overlay a:hover::after {
  width: 100%;
} */

.nav-links-overlay a:hover {
  cursor: pointer;
}

.nav-links-overlay a::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -1px;
  width: 100%;
  height: 1px;
  background-color: rgb(185, 185, 185);
  transition: width 0.3s ease;
}

.nav-links-overlay a{
  text-shadow: 0 0 20px rgba(255, 255, 255, 0.5), 0 0 100px rgba(255, 255, 255, 0.5);
}

.nav-links-overlay a {
  font-size: 2rem;
  font-weight: bold;
  color: rgb(185, 185, 185);
  text-decoration: none;
  position: relative;
  transition: color 0.3s ease, text-shadow 0.3s ease;
}

.nav-links-overlay {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.nav-overlay.fade-in {
  opacity: 1;
  visibility: visible;
}

.nav-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.95);
  /* backdrop-filter: blur(3px); */
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s ease, visibility 0.2s ease;
  z-index: 200;
}