/* src/components/Gallery/Gallery.css */

.gallery {
    padding: 0px 1vw;
    padding-top: 50px;
    padding-bottom: 20px;
    background-color: #f9f9f9;
    opacity: 0;
    transform: translateY(50px);
    transition: opacity 1s ease, transform 1s ease;
  }
  
  .gallery.faded-in {
    opacity: 1;
    transform: translateY(0);
  }
  
  .gallery-title {
    text-align: center;
    font-size: 36px;
    margin-bottom: 60px;
    color: #333;
    letter-spacing: 1.2px;
  }
  
  .image-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
    gap: 50px;
    padding: 0 0px;
  }
  
  .image-wrapper {
    overflow: hidden;
    border-radius: 3px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.45s ease;
  }
  
  .image-wrapper:hover {
    transform: scale(1.02);
  }
  
  .gallery-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  @media (max-width: 1500px) {
    .gallery {
      padding: 0px 1vw;
      padding-top: 50px;
    }
  }
  /* Mobile responsiveness */
  @media (max-width: 768px) {
    .gallery-title {
      font-size: 28px;
    }

    .gallery {
      padding-top: 35px;
    }
  
    .image-grid {
      grid-template-columns: 1fr;
      gap: 35px;
    }

    .gallery-title {
      margin-bottom: 40px;
    }
  }
  @media (max-width: 600px) {
    .gallery {
      padding: 0px 2.5vw;
      padding-top: 35px;
    }
  }
  