/* Centering the entire services page content */
.services-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 60vh;
    text-align: center;
    padding: 20px;
    padding-top: 50px;
    padding-bottom: 60px;
    box-sizing: border-box;
    color: #333;
    background-color: #f9f9f9;
}

/* Title styling */
.services-title {
    font-size: 36px;
    margin-bottom: 10px;
    color: #333;
}

.services-tagline {
    font-size: 24px;
    font-weight: 400;
    margin-top: 10px;
    letter-spacing: 0.2px;
    animation: fadeIn 0.0s ease-in-out;
    width: 95%;
    text-align: center;
    margin-bottom: 60px;
}

@media (max-width: 768px) {
    .services-title {
        font-size: 28px;
    }

    .services-tagline {
        font-size: 19px;
    }
}

/* Container for the AnimatedComponent items */
.services-list {
    display: flex;
    flex-wrap: wrap;
    gap: 17.3px;
    /* Gap between items when displayed in row */
    justify-content: center;
    color: #333;
}

/* Style changes based on screen width */
@media (max-width: 1149px) {
    .services-list {
        flex-direction: column;
        /* Stack items vertically on smaller screens */
        gap: 0;
        /* No gap between items */
    }
}