/* Page background and text colors */
.about-section {
    background-color: #f9f9f9;
    color: #333;
    display: flex;
    justify-content: center;
    padding: 40px 0;
    box-sizing: border-box;
}

.about-content {
    width: 67vw;
    /* Width on larger screens */
    max-width: 1000px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}

.about-title-container {
    flex: 1;
    display: flex;
    flex-direction: row;
    gap: 50px;
}

.about-title-container h1 {
    font-size: calc(36px + 0vw);
    font-weight: bold;
    text-align: left;
    position: relative;
    line-height: calc(30px + 0vw);
    display: flex;
    flex-direction: column;
    white-space: nowrap;
    left: 0.25rem;
    /* Prevent text wrapping */
    /* vertical-align: top; */
    /* flex: 1.3; */
}

.about-title-container h1 span {
    white-space: nowrap;
    content: "";
    display: block;
    height: 1.3em;
}

.about-paragraph {
    /* flex: 5 */
}

.title-part1,
.title-part2 {
    opacity: 0;
    transition: transform 0.6s ease, opacity 0.6s ease;
}

/* Animation for title parts */
.slide-in-title .title-part1 {
    transform: translateX(-0.25rem);
    opacity: 1;
}

.slide-in-title .title-part2 {
    transform: translateX(0.25rem);
    opacity: 1;
    transition-delay: 0.1s;
}

.about-title-container p {
    font-size: calc(1rem + 0.4vw);
    text-align: left;
    line-height: 1;
    position: relative;
    /* vertical-align: top; */
}

.about-paragraph span::after {
    content: "";
    display: block;
    height: 0.5em;
}

.fade-in-words {
    opacity: 1;
    animation: wordFade 1s ease-in forwards;
}

@keyframes wordFade {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

/* Image container and styling */
.about-image-container {
    width: 100%;
    height: fit-content;
    max-height: 580px;
    border-radius: 3px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin-top: 20px;
}

.about-image-container img {
    width: 100%;
    height: auto;
    object-fit: cover;
    object-position: bottom;
    /* Bottom-aligned image */
}

/* Responsive adjustments for smaller screens */
@media (max-width: 1149px) {
    .about-content {
        width: 90vw;
        flex-direction: column;
        align-items: center;
    }

    .about-title-container {
        text-align: center;
        flex-direction: column;
        gap: 10px;
    }


}