.masonry-gallery {
  column-count: 3; /* Number of masonry columns */
  column-gap: var(--gallery-gap, 1vw); /* Customizable gap between images */
  width: 100%;
}

.masonry-item {
  break-inside: avoid;
  margin-bottom: var(--gallery-gap, 1vw);
}

.image-container {
  position: relative;
  overflow: hidden;
  border-radius: 3px;
  transition: box-shadow 0.5s ease;
  max-height: 90vh;
}

.image-container img {
  width: 100.1%;
  height: auto;
  display: block;
  object-fit: cover;
  transition: transform 0.5s ease;
}

/* Hover effect: zoom image and add shadow */
.image-container:hover img {
  transform: scale(1.015); /* Slight zoom on the image itself */
}

.image-container:hover {
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.278);
  cursor: pointer;
}

/* Title strip at the bottom */
.image-title {
  position: absolute;
  bottom: -0.5px;
  left: 0;
  width: 100.5%;
  background: rgba(0, 0, 0, 0.8);
  color: white;
  font-size: 1rem;
  padding: 8px;
  text-align: center;
  opacity: 0;
  /* transform: translateY(10%); */
  transition: transform 0.3s ease, opacity 0.7s ease;
  font-weight: 400;
  letter-spacing: 0.5px;
}

/* Show the title on hover */
.image-container:hover .image-title {
  opacity: 1;
  transform: translateY(0); /* Slide the title up */
}

@media (max-width: 1500px) {
  .masonry-gallery {
    column-count: 2; /* Fewer columns on smaller screens */
  }
}

@media (max-width: 600px) {
  .masonry-gallery {
    column-count: 1; /* Single column on mobile */
    column-gap: var(--gallery-gap, 2.5vw);
  }
  
  .masonry-item {
    margin-bottom: var(--gallery-gap, 2.5vw);
  }
}
