.contact-section {
    background-color: #f9f9f9;
    color: #333;
    text-align: center;
    padding: 20px;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .contact-icon {
    width: 50px;
    margin-bottom: 40px;
  }
  
  .contact-message {
    font-size: 18px;
    line-height: 1.6;
    max-width: 600px;
    margin: 0 auto 10px;
  }
  
  .contact-details {
    margin-top: 20px;
  }
  
  .contact-url {
    font-size: calc(36px + 1vw);
    margin-bottom: 10px;
  }
  
  .contact-reveal-link {
    display: inline-block;
    font-size: calc(36px + 1vw);
    color: #333;
    cursor: pointer;
    position: relative;
    margin: 10px 0;
    text-decoration: none;
  }
  
  .contact-revealed-link {
    text-decoration: none;
    color: #333;
  }
  
  .contact-reveal-link::after {
    content: "";
    position: absolute;
    bottom: -2px;
    left: 0;
    height: 2px;
    width: 0;
    background-color: #333;
    transition: width 0.15s ease-out, left 0.15s ease-out;
  }
  
  .contact-reveal-link:hover::after {
    width: 100%;
    left: 0;
  }
  
  .contact-reveal-link:not(:hover)::after {
    transition: width 0.15s ease-in, left 0.15s ease-in;
  }
  
  .contact-reveal-link:hover::after {
    width: 100%;
  }
  
  .contact-reveal-link:not(:hover)::after {
    width: 0;
  }

  @media (max-width: 700px) {

    .contact-url {
        font-size: calc(22px + 1vw);
      }
      
      .contact-reveal-link {
        font-size: calc(22px + 1vw);
      }
      .contact-message {
        font-size: 17px;
      }
      .contact-section {
        background-color: #f9f9f9;
        color: #333;
        text-align: center;
        padding: 30px;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }

  }
  