/* Hero Section Styling */
.hero-section {
  position: relative;
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  text-align: center;
}

/* Add a pseudo-element for the background */
.hero-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('https://i.postimg.cc/Th8Tww8G/pexels-roman-odintsov-8180457-2.jpg');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  z-index: 0; /* Ensure it stays behind other content */
  transform: scale(1.1); /* Start slightly zoomed in */
  animation: zoomOutBg 3s ease-in-out forwards;
}

/* Hero Content Styling */
.hero-content {
  animation: fadeIn 1.5s ease-in-out;
  transition: opacity 0.05s ease, filter 0.05s ease;
}

.hero-title {
  font-size: 64px;
  font-weight: bold;
  margin: 0;
  letter-spacing: 2px;
}

.hero-tagline {
  font-size: 24px;
  font-weight: 400;
  margin-top: 10px;
  letter-spacing: 1px;
  animation: fadeIn 2s ease-in-out;
}

.scroll-down {
  position: absolute;
  bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: fadeIn 2s ease-in-out;
}

.scroll-text {
  margin-top: 10px;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 1px;
  opacity: 0.8;
}

.animated-line {
  width: 2px;
  height: 0;
  background-color: white;
  animation: alternateExtendShrink 3s infinite;
}

/* Animations */
@keyframes zoomOutBg {
  0% {
    transform: scale(1.1); /* Start slightly zoomed in */
  }
  100% {
    transform: scale(1); /* End at original scale */
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes alternateExtendShrink {
  0% {
    height: 0;
    margin-bottom: 50px;
  }
  50% {
    height: 50px;
    margin-bottom: 0;
  }
  100% {
    height: 0;
    margin-bottom: 0;
  }
}

/* Responsiveness */
@media (max-width: 768px) {
  .hero-title {
    font-size: 48px;
  }

  .hero-tagline {
    font-size: 19px;
  }
}
