/* Container and Header */
.team-container {
    background-color: #f9f9f9;
    color: #333;
    width: 67vw;
    max-width: 1000px;
    margin: 0 auto;
    padding-top: 2rem;
    padding-bottom: 2rem;
  }
  
  @media (max-width: 768px) {
    .team-container {
      width: 90vw;
    }
  }
  
  .team-header {
    text-align: left;
    font-size: calc(36px + 0vw);
    margin-bottom: 2rem;
    font-weight: bold;
  }
  
  /* Grid Layout */
  .team-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1.5rem;
  }
  
  /* Card Styles */
  .team-card {
    display: flex;
    flex-direction: column;
    border-radius: 3px;
    overflow: hidden;
    background-color: #fff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    max-height: 90vh;
  }
  
  .team-card-top {
    flex: 2;
  }
  
  .team-photo {
    width: 100%;
    height: auto;
    object-fit: cover;
  }
  
  .team-card-bottom {
    flex: 1;
    padding: 1rem;
    text-align: left;
  }
  
  /* Name and Role */
  .team-name {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 1rem;
  }
  
  .team-role-container {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    margin-bottom: 0.5rem;
  }
  
  .team-role {
    padding: 0.15rem 0.6rem;
    border: 1px solid #333;
    border-radius: 20px;
    font-size: 0.9rem;
  }
  
  .linkedin-icon {
    color: #333;
    font-size: 1.2rem;
    transition: color 0.3s ease;
  }
  
  .linkedin-icon:hover {
    color: #0077b5;
  }
  
  /* Bio */
  .team-bio {
    font-size: 0.9rem;
    color: #333;
    /* background-color: ; */
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    border-radius: 4px;
  }
  