/* Basic styling for the container */
.animated-component {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 350px;
  height: 350px;
  padding-left: 35px;
  overflow: hidden;
  margin-left: 30px;
  margin-right: 30px;
}

.icon-container {
  margin-top: 18px;
}

.bottom-text-container {
  margin-bottom: 18px;
}

/* The vertical line styling and animation */
.vertical-line {
  position: absolute;
  left: 0;
  width: 3px;
  background-color: #000000;
  height: 100%;
  bottom: 0;
  transform: scaleY(0);
  /* Start from the bottom */
  transform-origin: bottom;
  /* Extend up from the bottom */
  transition: transform 0.7s ease-in-out;
}

.service-label {
  font-size: 18px;
}

.animate-line {
  transform: scaleY(1);
  /* Full extension upwards */
}

/* Icon container at the top */
.icon-container {
  display: flex;
  align-items: left;
  justify-content: flex-start;
  height: 25%;
  opacity: 0;
  transition: opacity 0.25s ease-in-out 0.1s;
  /* Initial fade-in for icon */
  transform: translateY(0);
  /* Remove unnecessary translation */
}

.icon-container.fade-in {
  opacity: 1;
}

.icon svg {

}


/* Text container for the big text */
.text-container {

  height: 50%;
  padding-top: 10px;
  opacity: 0;
  transition: opacity 0.25s ease-out 0.25s, transform 0.25s ease-out 0.25s;
  /* Starts after icon fade-in */
  transform: translateY(-5px);
  /* Slightly reduced translation to avoid overlap */
  font-size: 30px;
  text-align: left;
}

.text-container.fade-in-delayed-1 {
  opacity: 1;
  transform: translateY(0);
}

/* Bottom text container for the normal text */
.bottom-text-container {
  height: 25%;
  display: flex;
  align-items: flex-end;
  opacity: 0;
  transition: opacity 0.25s ease-out 0.5s, transform 0.25s ease-out 0.5s;
  /* Starts after big text animation */
  transform: translateY(-3px);
  /* Slightly reduced translation to avoid overlap */
  letter-spacing: 0.1px;
  font-size: 20px;
  /* overflow: hidden; */
  overflow: hidden;
  text-align: left;
  
}

.bottom-text-container.fade-in-delayed-2 {
  opacity: 1;
  transform: translateY(0);
}

.bottom-text-container p {
  margin: 0;
  padding: 0px 0;
}

/* Responsive styling */
@media (max-width: 768px) {
  .animated-component {
    width: 350px;
    height: 350px;
    padding-left: 35px;
  }

  .icon-container {
    margin-top: 15px;
  }
  .service-label {
    font-size: 17px;
  }

  .text-container {
    /* height: 50%;
    padding-top: 10px;
    opacity: 0;
    transition: opacity 0.5s ease-in-out 0.5s, transform 0.5s ease-in-out 0.5s; */
    transform: translateY(-6px);
    font-size: 24px;
  }

  .bottom-text-container {
    /* height: 25%;
    display: flex;
    align-items: flex-end;
    opacity: 0;
    transition: opacity 0.5s ease-in-out 0.7s, transform 0.5s ease-in-out 0.7s; */
    transform: translateY(-6px);
    letter-spacing: 0px;
    font-size: 19px;
    margin-bottom: 15px;
  }
}

@media (max-width: 480px) {
  .animated-component {
    width: 300px;
    height: 300px;
    padding-left: 35px;
  }

  .icon-container {
    margin-top: 15px;
  }

  .service-label {
    font-size: 17px;
  }

  .text-container {
    /* height: 50%;
    padding-top: 10px;
    opacity: 0;
    transition: opacity 0.5s ease-in-out 0.5s, transform 0.5s ease-in-out 0.5s; */
    transform: translateY(-6px);
    font-size: 26px;
  }

  .bottom-text-container {
    /* height: 25%;
    display: flex;
    align-items: flex-end;
    opacity: 0;
    transition: opacity 0.5s ease-in-out 0.7s, transform 0.5s ease-in-out 0.7s; */
    transform: translateY(-6px);
    letter-spacing: 0px;
    font-size: 18px;
    margin-bottom: 15px;
  }
}