/* src/components/Footer/Footer.css */

.footer {
    background-color: #111;
    color: #fff;
    padding: 40px 20px;
    text-align: center;
  }
  
  .footer-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }
  
  .footer-logo h2 {
    font-size: 28px;
    margin-bottom: 8px;
  }
  
  .footer-logo p {
    font-size: 14px;
    color: #aaa;
  }
  
  .footer-links {
    list-style: none;
    display: flex;
    gap: 15px;
    padding: 0;
  }
  
  .footer-links a {
    text-decoration: none;
    color: #aaa;
    font-size: 14px;
    transition: color 0.3s;
  }
  
  .footer-links a:hover {
    color: #fff;
  }
  
  .footer-social a {
    margin: 0 10px;
    font-size: 24px;
    color: #aaa;
    transition: color 0.3s;
  }
  
  .footer-social a:hover {
    color: #fff;
  }
  
  .footer-copyright {
    margin-top: 20px;
    font-size: 12px;
    color: #777;
  }
  
  /* Responsive design for smaller screens */
  @media (max-width: 600px) {
    .footer-content {
      flex-direction: column;
      align-items: center;
    }
  
    .footer-links {
      flex-direction: column;
      gap: 10px;
    }
  }
  