/* Gallery Container */
.unique-gallery {
    display: flex;
    flex-direction: column;
    gap:
        /*7.7px*/
        1vw;
}

/* Rows Styling */
.unique-gallery-row {
    display: flex;
    gap:
        /*7.7px*/
        1vw;
}

.row-three .unique-gallery-image-container {
    width: calc(100vw / 3 - 1vw / 3);
    /* width: calc(100vw / 3 - 7.7px / 3); */
    height: 92.1vh;
}

.row-two .unique-gallery-image-container {
    width: calc(100vw / 2 - 1vw / 2);
    /* width: calc(100vw / 2 - 7.7px / 2); */
    height: 92.1vh;
}

.row-one .unique-gallery-image-container {
    width: 100vw;
    height: 92.1vh;
}

/* Image Container */
.unique-gallery-image-container {
    position: relative;
    overflow: hidden;
    border-radius: 3px;
    transition: box-shadow 0.5s ease;
}

.unique-gallery-image {
    width: 100.5%;
    height: 100.5%;
    object-fit: cover;
    object-position: center;
    transition: transform 0.5s ease, box-shadow 0.3s ease;
    transform: scale(1.05);
}

.unique-gallery-image-container:hover .unique-gallery-image {
    transform: scale(1.065);
    /* box-shadow: 0 4px 15px rgb(0, 0, 0); */
}

.unique-gallery-image-container:hover {
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

/* Hover Overlay */
.unique-gallery-hover-overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 8px;
    /* height: 7.3%; */
    background-color: rgba(0, 0, 0, 0.7);
    opacity: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: opacity 0.7s ease;
    backdrop-filter: blur(5px);

}

.unique-gallery-image-container:hover .unique-gallery-hover-overlay {
    opacity: 1;
}

.unique-gallery-title {
    color: white;
    font-size: 1rem;
    font-weight: 400;
    text-align: center;
}

/* Responsive Design */
@media (max-width: 768px) {

    /* Gallery Container */
    .unique-gallery {
        gap:
            /*7.7px*/
            2.5vw;
    }

    .unique-gallery-row {
        flex-direction: column;
        gap: 2.5vw;
    }

    .unique-gallery-image-container {
        width: 100%;
    }

    .row-three .unique-gallery-image-container {
        /* width: calc(96vw); */
        width: 100%;
        height: auto;
        max-height: 97vh;
        margin: auto
    }

    .row-two .unique-gallery-image-container {
        /* width: calc(96vw); */
        width: 100%;
        height: auto;
        max-height: 97vh;
        margin: auto
    }

    .row-one .unique-gallery-image-container {
        /* width: calc(96vw); */
        width: 100%;
        height: auto;
        max-height: 97vh;
        margin: auto
    }

    .unique-gallery-image-container {
        pointer-events: none;
    }

    .unique-gallery-hover-overlay {
        opacity: 1 !important;
    }

    /* .unique-gallery-image {
        height: 150%;
    } */
}