
  .highlight-container {
    position: relative;
    display: inline-block;
  }
  
  .underline {
    position: absolute;
    bottom: -2px;
    left: 0;
    background-color: #333;
    border-radius: 2px;
    width: 100%;
    transform-origin: left center;
    transform: scaleX(0);
    transition: transform 0.6s ease-in-out 0.4s;
    max-height: 2.4px;
    min-height: 1.7px;
  }
  
  /* Animation trigger */
  .underline.animate {
    transform: scaleX(1);
  }
  
  /* Responsive line thickness */
  @media (max-width: 600px) {
    .underline {
      height: 1px;
    }
  }
  
  @media (min-width: 601px) and (max-width: 1024px) {
    .underline {
      height: 2px;
    }
  }
  
  @media (min-width: 1025px) and (max-width: 1440px) {
    .underline {
      height: 3px;
    }
  }
  
  @media (min-width: 1441px) {
    .underline {
      height: 4px;
    }
  }
  