/* Regular variable font */
@font-face {
  font-family: 'Playfair Display';
  src: url('./assets/fonts/Playfair Display/PlayfairDisplay-VariableFont_wght.ttf') format('truetype');
  font-weight: 100 900; /* Variable weight range */
  font-style: normal;
}

/* Italic variable font */
@font-face {
  font-family: 'Playfair Display';
  src: url('./assets/fonts/Playfair Display/PlayfairDisplay-Italic-VariableFont_wght.ttf') format('truetype');
  font-weight: 100 900; /* Variable weight range */
  font-style: italic;
}


@font-face {
  font-family: 'Arima';
  src: url('./assets/fonts/Arima/Arima-VariableFont_wght.ttf') format('truetype');
  font-weight: 100; /* Define minimum weight */
  font-weight: 900; /* Define maximum weight */
  font-style: normal; /* Specify style */
}

/* Regular variable font */
@font-face {
  font-family: 'Aleo';
  src: url('./assets/fonts/Aleo/Aleo-VariableFont_wght.ttf') format('truetype');
  font-weight: 100 900; /* Variable range from 100 to 900 */
  font-style: normal;
}

/* Italic variable font */
@font-face {
  font-family: 'Aleo';
  src: url('./assets/fonts/Aleo/Aleo-Italic-VariableFont_wght.ttf') format('truetype');
  font-weight: 100 900; /* Variable range from 100 to 900 */
  font-style: italic;
}


/* Add the remaining font faces similarly for other weights and styles */


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Playfair Display';
  font-style: normal;
}

